* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__logo, .menu__list {
  align-items: center;
  display: flex;
}

.menu__item {
  margin-left: 20px;
  list-style-type: none;
}

.menu__item:first-child {
  margin-left: 0;
}

.menu__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.menu__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  text-decoration: underline;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

:root {
  --s: 1;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  align-self: flex-start;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-price {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__btn:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #f3f3f3;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.main {
  grid-template-columns: repeat(var(--s), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --s: 2;
  }
}

@media (width >= 768px) {
  .main {
    --s: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --s: 4;
  }
}
